.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerRegular.woff2") format("woff2"), url("../fonts/MullerRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerMedium.woff2") format("woff2"), url("../fonts/MullerMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  overflow-x: hidden;
  box-sizing: border-box;
  height: 100%;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
html > body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Muller", "Arial", "sans-serif";
}
html > body > main {
  overflow: hidden;
  flex-grow: 1;
}
html > body > main h2 {
  font-size: 40px;
  line-height: 50px;
  font-weight: 500;
}
html > body > footer {
  flex-shrink: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  width: 100%;
  height: auto;
}
@media (max-width: 767px) {
  html > body > main h2 {
    font-size: 32px;
    line-height: 40px;
  }
}
.container {
  width: 52.3%;
  min-width: 750px;
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 1159px) {
  .container {
    width: 80%;
    min-width: auto;
    max-width: 750px;
  }
  .container--styles {
    width: 70%;
  }
}
@media (max-width: 767px) {
  .container {
    width: 100%;
  }
}
html > body > header {
  width: 100%;
  height: 100vh;
}
.header-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1880px;
  height: 100%;
  padding: 20px;
  margin: auto;
}
.header-logo {
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 111px;
  margin-right: 5%;
  margin-left: 5%;
  font-size: 21px;
  line-height: 25px;
}
.header-logo > img {
  width: 200px;
  height: 55px;
}
.header-logo > a {
  display: flex;
  margin-bottom: 10px;
}
.header-logo > a::before {
  margin-right: 6px;
  width: 24px;
  height: 24px;
  content: "";
  background: url("../img/phone.svg") center center no-repeat;
}
.header-logo > a:hover,
.header-logo > a:focus {
  color: #F9572E;
  outline: none;
}
.header-logo > a:active {
  opacity: 0.7;
}
.header-promo {
  display: flex;
  flex-direction: column;
  flex-basis: 33%;
  min-width: 535px;
  height: 100%;
  padding-top: 111px;
  padding-bottom: 27px;
  padding-left: 4.5%;
  margin-top: -111px;
}
.header-promo h1 {
  margin-top: auto;
  margin-bottom: 6px;
  font-size: 64px;
  line-height: 64px;
  font-weight: 500;
  letter-spacing: -0.5px;
}
.header-promo > p {
  width: 90%;
  padding-left: 6px;
  margin-bottom: 45px;
  font-size: 21px;
  line-height: 30px;
}
.promo-subscribe {
  display: flex;
  align-items: center;
  margin-left: 7px;
  margin-bottom: 80px;
}
.promo-subscribe > small {
  margin-left: 34px;
  font-size: 16px;
  line-height: 24px;
  color: #666666;
}
.header-promo > a:last-of-type {
  position: relative;
  padding-left: 65px;
  margin-top: auto;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  color: #F9572E;
}
.header-promo > a:last-of-type::before {
  position: absolute;
  left: 2%;
  width: 41px;
  height: 32px;
  content: "";
  background: url("../img/arrows.svg") center center no-repeat;
}
.header-promo > a:last-of-type:hover,
.header-promo > a:last-of-type:focus {
  outline: none;
}
.header-promo > a:last-of-type:hover::before,
.header-promo > a:last-of-type:focus::before {
  background-image: url("../img/arrows_hover.svg");
}
.header-promo > a:last-of-type:active {
  opacity: 0.7;
}
.header-video {
  position: relative;
  flex-basis: 51%;
  height: 100%;
  margin-top: -111px;
}
.header-video > img {
  display: block;
  height: 100%;
  object-fit: cover;
}
.header-video > button {
  position: absolute;
  right: 0;
  bottom: 50%;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  font-size: 21px;
  line-height: 30px;
  cursor: pointer;
  color: #F9572E;
  background: none;
  border: none;
  transform: translateY(40%);
}
.header-video > button::before {
  margin-bottom: 16px;
  width: 255px;
  height: 255px;
  content: "";
  background: url("../img/play.svg") center center no-repeat;
  background-size: contain;
}
.header-video > button:hover,
.header-video > button:focus {
  outline: none;
}
.header-video > button:hover::before,
.header-video > button:focus::before {
  background: url("../img/play_hover.svg") center center no-repeat;
  background-size: contain;
}
.header-video > button:active {
  opacity: 0.7;
}
@media (max-width: 1159px) {
  html > body > header {
    flex-direction: column;
    height: auto;
  }
  .header-promo {
    flex-basis: auto;
    height: 100vh;
    padding-right: 4.5%;
    margin-top: -131px;
    margin-bottom: 20px;
  }
  .header-video {
    width: 100%;
    min-width: 100%;
    height: 60vw;
    margin-top: 50px;
  }
  .header-video > button {
    transform: translateY(50%);
  }
}
@media (max-width: 767px) {
  .header-wrapper {
    padding: 20px 0;
  }
  .header-logo {
    height: 50px;
  }
  .header-logo > img {
    width: 160px;
    height: 44px;
  }
  .header-logo > a {
    margin-bottom: 0;
    font-size: 0;
  }
  .header-promo {
    align-items: center;
    min-width: 0;
    max-width: 465px;
    padding: 140px 20px 20px;
    margin: -70px auto 0 auto;
  }
  .header-promo h1 {
    font-size: 36px;
    line-height: 36px;
  }
  .header-promo > p {
    width: 100%;
    padding-left: 0;
    margin-bottom: 60px;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
  }
  .header-promo > a:last-of-type {
    align-self: flex-start;
    font-size: 14px;
    line-height: 16px;
  }
  .promo-subscribe > small {
    display: none;
  }
  .header-video {
    height: 90vw;
  }
  .header-video > button::before {
    width: 100px;
    height: 100px;
  }
}
.features {
  padding-top: 133px;
  padding-bottom: 98px;
  font-size: 16px;
  line-height: 24px;
}
.features h2 {
  margin-bottom: 0;
}
.features > p {
  margin-top: 7px;
  margin-bottom: 60px;
  font-weight: 500;
  letter-spacing: 0.62px;
  text-transform: uppercase;
  color: #33B5A3;
}
.features ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 60px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.features li {
  flex-basis: 46%;
  padding: 35px 0 39px 0;
  margin-bottom: 56px;
  border-top: 4px solid #63D2C1;
}
.features li > b {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
}
.features li > p {
  margin: 10px 0 0 0;
  color: #666666;
}
.features li:last-of-type {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 35px;
  text-align: center;
  border: 4px solid #63D2C1;
}
.features li:last-of-type > p {
  font-size: 64px;
  line-height: 60px;
  font-weight: 500;
  color: #000000;
}
@media (max-width: 1159px) {
  .features {
    padding-top: 80px;
  }
  .features ul {
    margin-right: -10px;
    margin-left: -10px;
  }
  .features li {
    flex-grow: 1;
    min-width: 320px;
    margin: 0 10px;
  }
}
@media (max-width: 767px) {
  .features {
    padding: 50px 20px 50px;
    text-align: center;
  }
  .features li {
    min-width: auto;
  }
  .features > p {
    margin-top: 20px;
    font-size: 14px;
  }
  .features li:last-of-type > p {
    font-size: 40px;
    line-height: 40px;
    font-weight: 500;
    color: #000000;
  }
}
.styles {
  position: relative;
  margin-bottom: 161px;
}
.styles h2 {
  margin-bottom: 0;
}
.styles > p {
  margin-top: 7px;
  margin-bottom: 50px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #33B5A3;
}
.styles ul {
  display: flex;
  height: 500px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.styles li {
  position: relative;
  display: flex;
  overflow: hidden;
  flex-shrink: 0;
  width: 100%;
  height: auto;
  transition: left, 0.5s ease-in-out;
}
.styles li > picture,
.styles li > img {
  flex-grow: 1;
}
.styles li img {
  min-width: 100%;
  min-height: 100%;
  opacity: 0.6;
  object-fit: cover;
  object-position: left;
}
.styles li > p {
  flex-grow: 0;
  flex-shrink: 0;
  width: 16.5%;
  margin: 0;
  transition: all 0.5s;
}
.styles li > p span {
  display: none;
  font-size: 13px;
  line-height: 20px;
  color: #666666;
  transition: all 0.5s;
}
.styles li.active p {
  width: 23%;
  padding-left: 1%;
  padding-right: 5%;
}
.styles li.active > p span {
  display: initial;
}
.styles li.active img {
  opacity: 1;
}
.styles > div {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  width: 23%;
}
.styles > div > button {
  margin: 0 5px;
  width: 44px;
  height: 44px;
  cursor: pointer;
  background: url("../img/single-arrow.svg") center center no-repeat;
  border: none;
}
.styles > div > button:first-of-type {
  transform: rotate(-180deg);
  transform-origin: center;
}
.styles > div > button:hover {
  background-image: url("../img/single-arrow_hover.svg");
}
.styles > div > button:active {
  opacity: 0.7;
}
.styles > div > button:disabled {
  visibility: hidden;
}
@media (max-width: 1159px) {
  .styles ul {
    height: max-content;
  }
  .styles li img {
    display: block;
    height: 35vw;
  }
}
@media (max-width: 767px) {
  .styles {
    margin-bottom: 90px;
  }
  .styles h2,
  .styles > p {
    margin-right: 15px;
    margin-left: 15px;
    text-align: center;
  }
  .styles li {
    flex-direction: column;
  }
  .styles li img,
  .styles > div > button {
    height: 65vw;
  }
  .styles li > p,
  .styles li.active p {
    height: 70px;
    width: auto;
    padding: 10px 10px 0;
    text-align: center;
  }
  .styles > div {
    bottom: 70px;
    z-index: 1;
    justify-content: space-between;
    width: 100%;
  }
}
.subscribe {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 55px;
  margin-bottom: 134px;
  border: 4px solid #63D2C1;
}
.subscribe h2 {
  z-index: 2;
  width: max-content;
  max-width: 80%;
  padding: 0 15px;
  margin: 0 auto;
  text-align: center;
  background-color: #FFFFFF;
  transform: translateY(-50%);
}
.subscribe form > p,
.subscribe-success > p {
  align-items: center;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
}
.subscribe form > p {
  display: flex;
  max-width: 368px;
  margin-bottom: 50px;
}
.subscribe form > p::before {
  flex-shrink: 0;
  width: 46px;
  height: 69px;
  margin-right: 15px;
  content: "";
  background: url("../img/door.svg") center center no-repeat;
}
.subscribe-success {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: #FFFFFF;
}
.subscribe-success p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 310px;
  height: 100%;
  margin: auto;
  text-align: center;
}
.subscribe-success p::before {
  width: 214px;
  height: 150px;
  margin-bottom: 35px;
  background: url("../img/mark.svg") center center no-repeat;
  background-size: contain;
  content: "";
}
.subscribe form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 500px;
}
.subscribe label {
  position: relative;
  width: 100%;
  margin-bottom: 30px;
}
.subscribe label > span {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  padding: 0 7px;
  margin-left: 18px;
  font-size: 13px;
  line-height: 24px;
  color: #666666;
  background-color: #FFFFFF;
  transform: translateY(-50%);
}
.subscribe input {
  height: 60px;
  width: 100%;
  padding: 10px 25px;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
  outline: none;
}
.subscribe input::placeholder {
  font-size: 18px;
  color: #666666;
  opacity: 1;
}
.subscribe input:hover {
  box-shadow: 0 0 1px 2px #CCCCCC;
}
.subscribe input:invalid {
  border-color: #F9572E;
  outline: none;
  box-shadow: none;
}
.subscribe input:invalid + span {
  display: initial;
  color: #F9572E;
}
.subscribe input:valid + span {
  display: initial;
}
.subscribe input.reset {
  border: 1px solid #CCCCCC;
}
.subscribe input.reset + span {
  display: none;
  color: #666666;
}
.subscribe form > button {
  min-width: 250px;
  margin-top: 18px;
  margin-bottom: 22px;
  cursor: pointer;
  border: none;
}
.subscribe form > small {
  font-size: 13px;
  line-height: 20px;
  color: #666666;
}
.subscribe form > a {
  font-size: 13px;
  line-height: 20px;
  color: #F9572E;
}
.subscribe form > a:hover,
.subscribe form > afocus {
  text-decoration: underline;
  outline: none;
}
.subscribe form > a:active {
  opacity: 0.7;
}
@media (max-width: 767px) {
  .subscribe {
    margin-bottom: 40px;
    text-align: center;
    border: none;
  }
  .subscribe h2 {
    width: auto;
    max-width: 500px;
    padding: 0;
    margin: 0;
  }
  .subscribe form {
    padding-right: 15px;
    padding-left: 15px;
  }
  .subscribe form > p {
    margin-top: 0;
    font-size: 14px;
    line-height: 20px;
  }
  .subscribe form > p::before {
    content: none;
  }
  .subscribe-success p::before {
    width: 107px;
    height: 75px;
  }
}
html > body > footer {
  padding-top: 73px;
  text-align: center;
  background-color: #F2F2F2;
}
html > body > footer h2 {
  margin-top: 35px;
  margin-bottom: 0;
  font-size: 21px;
  line-height: 26px;
  font-weight: 400;
}
.footer-socials {
  margin-bottom: 75px;
}
.footer-socials li > a {
  display: block;
  margin: 0 15px;
  padding: 13px 9px;
  width: 50px;
  height: 50px;
  background-color: #F9572E;
}
.footer-socials li > a:hover,
.footer-socials li > a:focus {
  background-color: #FF7B59;
  outline: none;
  box-shadow: 0px 15px 20px rgba(249, 87, 46, 0.7);
}
.footer-socials li > a:active {
  opacity: 0.7;
}
.footer-socials a img {
  height: 100%;
  object-fit: contain;
}
.footer-socials ul {
  display: flex;
  justify-content: center;
  margin-bottom: 32px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-socials > p,
.footer-contacts > p > span,
.footer-copyright {
  font-size: 16px;
  line-height: 24px;
  color: #666666;
}
.footer-socials > p {
  width: 448px;
  margin: 5px auto 0 auto;
}
.footer-contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding-right: 15px;
  margin-bottom: 60px;
  font-size: 21px;
  line-height: 30px;
  letter-spacing: -0.1px;
}
.footer-contacts > p {
  display: flex;
  flex-direction: column;
  margin: 0 10px 8px 45px;
}
.footer-contacts > a {
  display: flex;
  align-items: center;
}
.footer-contacts > a::before {
  margin-right: 9px;
  width: 30px;
  height: 24px;
  content: "";
  background: url("../img/phone.svg") center center no-repeat;
}
.footer-contacts > a:last-of-type::before {
  background: url("../img/mail.svg") center center no-repeat;
}
.footer-contacts > a:last-of-type span {
  border-bottom: 2px solid #F9572E;
}
.footer-contacts > a:hover,
.footer-contacts > a:focus {
  color: #F9572E;
  outline: none;
}
.footer-contacts > a:active {
  opacity: 0.7;
}
.footer-copyright {
  padding: 35px;
  margin: 0;
  border-top: 1px solid #CCCCCC;
}
@media (max-width: 1159px) {
  .footer-contacts {
    justify-content: center;
  }
  .footer-contacts > p {
    order: -1;
    flex-basis: 100%;
    margin: 0 0 20px 0;
  }
  .footer-contacts > a {
    margin: 20px;
  }
}
@media (max-width: 767px) {
  html > body > footer {
    padding: 50px 15px 0;
  }
  .footer-socials > p {
    display: none;
  }
  .footer-contacts {
    flex-direction: column;
    margin-bottom: 45px;
  }
  .footer-contacts > a {
    font-size: 18px;
    line-height: 28px;
  }
  .footer-copyright {
    padding: 20px;
  }
}
.button {
  padding: 16px 29px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  text-align: center;
  color: #FFFFFF;
  background-color: #F9572E;
  border-radius: 40px;
  box-shadow: 0px 7px 15px rgba(249, 87, 46, 0.7);
}
.button:hover,
.button:focus {
  background-color: #FF7B59;
  outline: none;
  box-shadow: 0px 15px 20px rgba(249, 87, 46, 0.7);
}
.button:active {
  opacity: 0.7;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
}
.hidden {
  display: none;
}
